body {
  width: 80%;
  margin: 2em auto 0 auto;
}

h1 span {
  background: -webkit-linear-gradient(#af08f1, #2d48df);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.images {
  -webkit-column-count: 2;
          column-count: 2;
}

img {
  width: 100%;
}

img:hover {
  cursor: pointer;
}

button,
label {
  background: -webkit-linear-gradient(#af08f1, #2d48df);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 16px;
}

button:hover {
  background: -webkit-gradient(linear, left top, right top, from(#af08f1), to(#2d48df));
  background: -webkit-linear-gradient(left, #af08f1, #2d48df);
  background: -o-linear-gradient(left, #af08f1, #2d48df);
  background: linear-gradient(to right, #af08f1, #2d48df);
  -webkit-text-fill-color: #fff;
}

.modal {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  padding: 15px;
  background: rgba(0, 0, 0, 0.17);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -webkit-animation-name: modalFade;
          animation-name: modalFade;
  -webkit-animation-duration: 0.3s;
          animation-duration: 0.3s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}

@-webkit-keyframes modalFade {
  from {
    -webkit-transform: translateX(-75%);
            transform: translateX(-75%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

@keyframes modalFade {
  from {
    -webkit-transform: translateX(-75%);
            transform: translateX(-75%);
    opacity: 0;
  }
  to {
    -webkit-transform: translateX(0);
            transform: translateX(0);
    opacity: 1;
  }
}

.modal-top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  margin-bottom: 5px;
}

.modal img {
  width: 100%;
  height: 95%;
  -o-object-fit: contain;
     object-fit: contain;
  -ms-flex: 1 1;
      flex: 1 1;
}

.modal span {
  padding: 2px 4px;
  font-weight: bold;
  line-height: 20px;
  font-size: 20px;
  color: #fcc600;
  border: 2px solid #fcc600;
  border-radius: 5px;
  -webkit-transition: all 0.1s ease;
  -o-transition: all 0.1s ease;
  transition: all 0.1s ease;
}

.modal span:hover {
  cursor: pointer;
  color: #fff;
  border: 2px solid #ffffff;
}

.modal a {
  color: #fcc600;
  padding: 5px;
  text-decoration: none;
  font-size: 16px;
  -webkit-transition: all 0.2s ease-out;
  -o-transition: all 0.2s ease-out;
  transition: all 0.2s ease-out;
}

.modal a:hover {
  color: #ddd;
}



